<!-- "" -->
<template>
    <div id="map"></div>
</template>

<script>
export default {
    mounted: function () {
        this.setMap_height()
        this.asyncLoadMap()
        this.getRegionPointList()                                                           // 
        this.setBottomBtnState(2)
    },
    methods: {
        setMap_height: function(){
            // 
            const   windowHeight = window.innerHeight;                                      //  
            const   bottomNav = document.getElementsByClassName('mu-paper')[0];             //  
            const   bottomNav_height = bottomNav.offsetHeight;                              //  
            let     map_height = windowHeight - bottomNav_height;                           //   
            const   objMap = document.getElementById('map');                                //  Map
            objMap.style.height = map_height + 'px';                                        //  Map
        }
        ,asyncLoadMap: function() {
            setTimeout(() => {
                const miniMap = new BMap.Map("map", {enableMapClick:false});                // Map()
                const miniMapPoint = new BMap.Point(120.378522,36.113888);                   // (  )
                miniMap.centerAndZoom(miniMapPoint,11);                                     // ,15
                miniMap.addControl(new BMap.NavigationControl({
                    anchor: BMAP_ANCHOR_TOP_RIGHT,
                    type: BMAP_NAVIGATION_CONTROL_SMALL
                }));
                /*    */
                // 
                let lastLevel;
                miniMap.addEventListener("zoomstart",function(){
                    lastLevel = this.getZoom();
                });
                miniMap.addEventListener("zoomend", function(){
                    let zoomLevel = this.getZoom();     //
                    if (zoomLevel >= 15){                                                           // 3:
                        addBuilding(BuildingModel,17);
                        // console.log("3:");
                    }else if (zoomLevel >= 14){
                        addRangeOverlay(businessCirclePoint,16);
                        // console.log("2:");                                      // 
                    }else{
                        if (!lastLevel < 14) {
                            addRangeOverlay(getRegionPointList,14);                                        // 
                            // console.log("1:");
                        }
                    }
                });
                //    (  +  +  )
                const buildingOverlayArr = []                                                       // 
                const addRangeOverlay = ( ObjGroup,setZoom ) => {                                   // 1 + 2 
                    miniMap.clearOverlays()                                                         // 
                    for (let i = 0; i < ObjGroup.length; i++) {
                        let arr = new Object()
                            arr = ObjGroup[i]
                        let code = arr.code,
                            url = arr.url,
                            text = arr.name + "<br />" + arr.resourceAmount + ""                   // 
                        let zoom = setZoom                                                          // 
                        let RangeOverlay = new rangeOverlay(
                            new BMap.Point(arr.latitude,arr.longitude),text,code,url,zoom
                        )
                        miniMap.addOverlay(RangeOverlay)
                    }
                };
                const addBuilding = ( ObjGroup,setZoom ) => {
                    miniMap.clearOverlays()                                                         // 
                    for (let i = 0; i < ObjGroup.length; i++) {
                        let buildingArr = new Object()
                            buildingArr = ObjGroup[i]
                        let zoom = setZoom                                                         // 
                        // 
                        let text = "" + buildingArr.priceBeginning +  " ",
                            mouseoverTxt = buildingArr.name + " " + buildingArr.resourceAmount + ""
                        buildingOverlayArr[i] = BuildingOverlay = new buildingOverlay(
                            new BMap.Point(buildingArr.latitude,buildingArr.longitude),text,mouseoverTxt,buildingArr.code,i,zoom    // i = 
                        )
                        miniMap.addOverlay(BuildingOverlay)
                        buildingOverlayArr[i] = BuildingOverlay
                    }
                };
                // ( - )
                addRangeOverlay()
                addBuilding()
            },50)
        }
        // : 
        ,getRegionPointList() {
            this.$store.dispatch({
                type: 'getRegionPointList',
                cityCode: this.$store.state.city.cityCode                                   // 
            })
        }
        //  (  +  +  )
        ,addOverlay() { }
        // 
        ,setBottomBtnState(state) {
            let bottomBtn_Arr = document.getElementsByClassName("mu-buttom-item")
            // 
            for( let i = 0; i < bottomBtn_Arr.length; i++ ) {
                bottomBtn_Arr[i].setAttribute("class","mu-buttom-item router-link-active")
            }
            bottomBtn_Arr[state].setAttribute("class","mu-buttom-item router-link-active mu-bottom-item-active")        // ""
        }
    }
}
</script>

<style lang="sass" scoped>
@import '../sass/main'

#map
</style>



